import React from 'react';
import { graphql } from 'gatsby';
import _ from 'lodash';
import Grid from '@material-ui/core/Grid';
import styled from 'styled-components';
import posed from 'react-pose';
import SEO from '../components/seo';
import Link from '../components/Link';
import Card from '../core/Card';
import Layout from '../components/Layout';

const ListContainer = posed.div({
  enter: {
    staggerChildren: 50,
  },
  exit: { staggerChildren: 20, staggerDirection: -1 },
});

const Item = posed.article({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0, transition: { duration: 200 } },
});
const StyledTitle = styled.h1`
  padding: 4rem 2rem;
`;
const StyledListContainer = styled(ListContainer)`
  padding: 0 2rem 4rem 2rem;
`;
const StyledItem = styled(Item)`
  background: #20232f;
`;
const CardLink = styled(Link)`
  :hover {
    color: #ffff !important;
  }
`;
const StyeldP = styled.p`
  font-size: 16px;
  font-weight: 300;
  margin: 10px 0;
`;
class BlogIndex extends React.Component {
  render() {
    const { data, location } = this.props;
    const posts = data.allMarkdownRemark.edges;

    return (
      <Layout location={this.props.location} theme="dark">
        <SEO title="All posts" keywords={['blog', 'gatsby', 'javascript', 'react']} />
        <StyledTitle>The notes from the books I have read</StyledTitle>

        <StyledListContainer initialPose="exit" pose="enter">
          <Grid container spacing={8}>
            {posts.map(({ node }) => {
              const title = node.frontmatter.title || node.fields.slug;
              return (
                <Grid item xs={12} sm={6} key={node.fields.slug}>
                  <StyledItem>
                    <CardLink to={`/${node.fields.slug}`}>
                      <Card title={title}>
                        <small>{node.frontmatter.date}</small>
                        <StyeldP dangerouslySetInnerHTML={{ __html: node.excerpt }} />
                      </Card>
                    </CardLink>
                  </StyledItem>
                </Grid>
              );
            })}
          </Grid>
        </StyledListContainer>
      </Layout>
    );
  }
}

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
          }
        }
      }
    }
  }
`;
